import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
	routes: [{
			path: "/",
			name: 'farm',
			component: () => import("@/view/LP/index.vue"),
		},
		{
			path: "/NFT",
			component: () => import("@/view/NFT/index.vue"),
		},
		{
			path: "/Me",
			component: () => import("@/view/Me/index.vue"),
		},
		{
			path: "/rules",
			component: () => import("@/view/rules/index.vue"),
		},
		{
			path: "/submit",
			component: () => import("@/view/SubmitApplicationInformation/index.vue"),
		},
		{
			path: "*",
			redirect: "/",
		},
	],
});
